.loader {
  width: 160px;
  height: 160px;
  border-radius: 50%;
  display: inline-block;
  position: relative;
  background: linear-gradient(0deg, rgba(255, 61, 0, 0.2) 33%, #ff3d00 100%);
  box-sizing: border-box;
  animation: rotation 1s linear infinite;
}

.loader::after {
  content: "";
  box-sizing: border-box;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 157px;
  height: 157px;
  border-radius: 50%;
  background: #fff;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

#logo-inside-loader {
  position: absolute;
  top: 65px;
  left: 55px;
}

.loader-container {
  width: 250px;
  height: 160px;
  position: relative;
  text-align: center;
}

.text-section {
  text-align: center;
  font-size: 15px;
  color: #939393;
  /* #f65c00; */
  font-weight: bold;
  font-family: serif;
}

.g-btn {
